import ErmjBaseView from "./ErmjBaseView";

export default class ErmjWinLightningView extends ErmjBaseView{
    private effectSk: sp.Skeleton;
    private contentNode: cc.Node;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.contentNode = this.getChild("content");
        this.effectSk = this.getComponent("content/effect", sp.Skeleton);
    }

    protected onOpen(){
        this.effectSk.node.active = true;
        this.effectSk.setAnimation(0, "idle", false);
        Game.Component.scheduleOnce(()=>{
            this.active = false;
        }, 1);
    }

    public setLightPoint(worldPos: cc.Vec3){
        this.contentNode.setPosition(this.contentNode.parent.convertToNodeSpaceAR(worldPos));
    }

    protected onClose(){
        this.effectSk.node.active = false;
        this.effectSk.clearTracks();
    }

    public clearByRound(){
        this.active = false;
    }
}